h1,
h2,
h3,
h4 {
  text-transform: uppercase;
  font-weight: bold !important; }

.zoom {
  overflow: hidden; }
  .zoom img {
    border-radius: 0;
    transition: 1s ease-out; }
  .zoom:hover img {
    -webkit-transform: scale3d(1.1, 1.1, 1);
    transform: scale3d(1.1, 1.1, 1); }

.wp-block-borncreative-header-block {
  position: relative;
  display: block; }
  .wp-block-borncreative-header-block .media {
    width: 100%;
    margin: 0;
    height: 100vh; }
    .wp-block-borncreative-header-block .media img {
      display: block;
      width: 100%;
      height: 100vh;
      object-fit: cover; }
  .wp-block-borncreative-header-block .copy {
    position: absolute;
    bottom: 1rem;
    left: 0;
    right: 0;
    color: white; }
    .wp-block-borncreative-header-block .copy .copy-hd {
      font-size: 2.5rem;
      line-height: 3.125rem;
      margin: 0;
      color: white; }
      .wp-block-borncreative-header-block .copy .copy-hd:before {
        display: none; }
    .wp-block-borncreative-header-block .copy .copy-bd {
      font-size: 2.125rem;
      line-height: 2.75rem;
      text-transform: none;
      margin: 0;
      color: white; }

.wp-block-borncreative-intro-text-block .copy {
